@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;600&family=Roboto:wght@400;500&display=swap');

* {
    margin: 0;
    padding: 0;
}

body {
    height: 100%;
    background: #fff;
}

ul {
    list-style-type: none;
    counter-reset: item;
}

header {
    height: 80px;
    background: #090808;
}

input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
}

input[type="number"] {
    -moz-appearance: textfield;
}

header .header-logo {
    position: relative;
    padding-left: 32px;
    padding-top: 8px;
}

main {
    position: relative;
    max-width: 1600px;
    min-height: 660px;
    margin: auto 160px;
}

main .greeting {
    position: absolute;
    max-width: 1600px;
    height: 84px;
    top: 80px;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 500;
    font-size: 72px;
    line-height: 84px;
}

main .annotation {
    position: absolute;
    max-width: 738px;
    height: 38px;
    top: 164px;

    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 32px;
    line-height: 38px;
}

main .list-box {
    box-sizing: border-box;
    position: absolute;
    width: 400px;
    height: 278px;
    top: 160px;
    right: 0;
    border: 1px solid #000000;

    @media(max-width: 1350px) {
        top: 220px;
        left: 50%;
        margin-left: -200px;
    }
}

main .list-box .record-number {
    position: absolute;
    width: 107px;
    height: 19px;
    left: 16px;
    top: 32px;

    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
}
main .list-box .input-number {
    box-sizing: border-box;
    position: absolute;
    width: 368px;
    height: 48px;
    left: 16px;
    top: 51px;
    border: 1px solid #000000;
    padding-left: 11px;

    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
}

main .list-box .record-name {
    position: absolute;
    width: 130px;
    height: 19px;
    left: 16px;
    top: 115px;

    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
}

main .list-box .input-name {
    box-sizing: border-box;
    position: absolute;
    width: 368px;
    height: 48px;
    left: 16px;
    top: 134px;
    border: 1px solid #000000;
    padding-left: 11px;

    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
}

main .list-box .add-button {
    position: absolute;
    width: 368px;
    height: 48px;
    left: 16px;
    top: 198px;
    background: #000000;

    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
    color: #FFFFFF;
}

main .list-item {
    position: absolute;
    max-width: 720px;
    height: 38px;
    top: 260px;

    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 32px;
    line-height: 38px;

    @media(max-width: 1350px) {
        top: 520px;
    }
}

footer {
    position: absolute;
    width: 100%;
    height: 160px;
    bottom: 0;
    left: 0;
    background: #737373;
}

footer .footer-logo {
    position: relative;
    padding-left: 32px;
    padding-top: 48px;
}